*{
    margin: 0;
    padding: 0;
}

a{
    text-decoration: none;
    padding: 10px;
    transition: .5s;
    margin: 0px;
}

a:hover{
    text-shadow: 0px 0px 10px #cccccc;
}
h1{
    font-size: 14px;
}

h2{
    font-size: 16px;
}

button{
    background-color: #3174f1;
    cursor: pointer;
}

button, input{
    border: none;
    border-radius: 5px;
    padding: 10px;
    width: 140px;
    margin: 10px;
    text-align: center;
    transition: .5s;
    box-shadow: 0px 0px 10px #cccccc;
}

button:hover, input:hover{
    width: 200px;
}

.app{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 360px;
}

.section{
    display: flex;
    border-radius: 8px;
    box-shadow: 0 0 50px #ccc;
    padding: 20px;
    margin: 15px;
}

.header-section, .footer-section{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 640px;
}

.header-logo{
    max-width: 56px;
    max-height: 52px;
    padding-right: 20px;
}

.logo-section{
    display: flex;
    align-items: center;
}

.navigation{
    display: flex;
}

.navigation a{
    color: black;
}

.headerStyle {
    flex-direction: row;
}

.footerStyle, .footer-nav{
    flex-direction: column;
    align-items: center;
}

.auth-section, .cdn-list, .cdn-form{
    flex-direction: column;
    align-items: center;
    min-width: 460px;

}

.footer-section{
    justify-content: space-between;
    align-items: flex-start;
    background-color: #000000;
}

.footer-section a,
.footer-section p{
    color: #cccccc;
}